* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
body, #root {
  width: 100vw;
  height: 100vh;
}
.gpx-submit {
  background-color: #c4ce6d;
  border-style: solid;
  border-color: black;
  border-radius: 50px;
  height: 20%;
  width: 50%;
  position: absolute;
  top: 50%;
  cursor: pointer;
  transition: 0.3s;
}
.gpx-submit:hover {
  background: #c7c0c0;
}
.gpx-form input[type="text"] {
  height: clamp(20px, 30%, 50px);
  width: 70%;
  border-radius: 20px;
  border-style: solid;
  position: absolute;
}
.gpx-form {
  width: clamp(200px, 50vw, 300px);
  height: clamp(150px, 30vh, 300px);
  border-style: solid;
  border-radius: 1px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50vw;
  top: 30vh;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
}
.quest-select-container {
  position: absolute;
  left: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: clamp(10rem, 30vw, 30rem);
  height: clamp(5rem, 20vh, 30rem);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.quest-select-container .select-quest, .quest-select-container .button-quest {
  cursor: pointer;
  width: clamp(10rem, 30vw, 20rem);
  height: clamp(2rem, 3vh, 5rem);
  border: 1px solid black;
  border-radius: 0.4rem;
  background-color: #3498db;
  border-color: #17252e;
  transition: 0.2s ease-in-out;
}
.quest-select-container .select-quest:hover, .quest-select-container .button-quest:hover {
  transition: 0.3s ease-in-out;
  color: aliceblue;
  background-color: #83725d;
}
.quest-select-container .select-quest .city, .quest-select-container .button-quest .city {
  margin-right: 1rem;
}
.quest-select-container .button-quest {
  width: clamp(10rem, 20vw, 20rem);
  height: clamp(1rem, 10vh, 2rem);
}
.quest-select-container .option-url {
  transition: 0.3s ease-in-out;
}